/*
  BEM BLOCK .c-notice-box
 */

.c-notice-box {
  display: block;
  @extend .o-box;
  background: $brand-col-3;
  text-align: center;
}

/*
  BEM BLOCK MODIFIER .c-notice-box--
 */

.c-notice-box-- {
  &type {
    display: block
  }
}

//BEM BLOCK MODIFIER FALLBACK .c-notice-box--

[class*=c-notice-box--] {
  @extend .c-notice-box;
}

/*
  BEM ELEMENTS .c-notice-box__
 */

.c-notice-box__ {
  &header {
    display: block
  }

  &heading {
    display: block;
    margin-top: 0;
  }

  &title {
    display: inline;
  }

  &link {
    display: inline
  }

  &content {
    display: block
  }

  &footer {
    display: block
  }
  &submit{
    transition: background 0.3s ease-in-out;
    width: 100%;
    display: block;
    text-align: center;
    color: #fff;
    background: $brand-col-4;
    border: none;
    padding: $inuit-global-spacing-unit-tiny;
    &:hover{
      background: darken($brand-col-4, 10%);
    }
  }
}

//BEM ELEMENT MODIFIER FALLBACK .c-notice-box--

[class*=c-notice-box__header--] {
  @extend .c-notice-box__header;
}

[class*=c-notice-box__heading--] {
  @extend .c-notice-box__heading;
}
